import * as React from 'react';
import { Color } from '../types';
import classnames from 'classnames';
import { ColorDot } from './ColorDot';
import { Progress, Save } from '../types';
import { hasLevel2, hasLevel3 } from '../utils';

export interface PuzzleSelectionProps {
  className?: string;
  levels: Color[];
  onRejectLevel: (level: Color) => () => void;
  onSelectLevel: (level: Color) => () => void;
  save: Save;
}

const PuzzleSelection: React.FC<PuzzleSelectionProps> = (props) => {
  const { className, save, levels, onRejectLevel, onSelectLevel } = props;
  const { Hidden } = Progress;
  const { Red, Green, Blue, Magenta, Cyan, Yellow, White } = Color;
  const { red, green, blue, magenta, cyan, yellow, white } = save;

  const renderLevel2 = () => {
    const hasLvl2 = hasLevel2(save);

    if (!hasLvl2) return null;

    return (
      <>
        <br />
        <h2>Floor 2</h2>
        <div className="block">
          {cyan !== Hidden && (
            <ColorDot
              className="black"
              color={Cyan}
              levels={levels}
              onRejectLevel={onRejectLevel(Cyan)}
              miniSave={save.cyan}
            />
          )}
          {magenta !== Hidden && (
            <ColorDot
              className="black"
              color={Magenta}
              levels={levels}
              onRejectLevel={onRejectLevel(Magenta)}
              miniSave={save.magenta}
            />
          )}
          {yellow !== Hidden && (
            <ColorDot
              className="black"
              color={Yellow}
              levels={levels}
              onRejectLevel={onRejectLevel(Yellow)}
              miniSave={save.yellow}
            />
          )}
        </div>
      </>
    );
  };

  const renderLevel3 = () => {
    const hasLvl3 = hasLevel3(save);

    if (!hasLvl3) return null;

    return (
      <>
        <br />
        <h2>Floor 3</h2>
        <div className="block">
          <ColorDot
            className="black"
            color={White}
            levels={levels}
            onRejectLevel={onRejectLevel(White)}
            miniSave={save.white}
          />
        </div>
      </>
    );
  };

  return (
    <>
      <h2>Floor 1</h2>
      <div className="block">
        <ColorDot
          className="black"
          color={Red}
          levels={levels}
          onSelectLevel={onSelectLevel(Red)}
          miniSave={save.red}
        />
        <ColorDot
          className="black"
          color={Green}
          levels={levels}
          onSelectLevel={onSelectLevel(Green)}
          miniSave={save.green}
        />
        <ColorDot
          className="black"
          color={Blue}
          levels={levels}
          onSelectLevel={onSelectLevel(Blue)}
          miniSave={save.blue}
        />
      </div>
      {renderLevel2()}
      {renderLevel3()}
    </>
  );
};

export { PuzzleSelection };
